@import url(https://fonts.googleapis.com/css?family=Alegreya&display=swap);
#nav-menu{
	display: flex;
	flex-direction: column;
}

#nav-menu > button{
	border: none;
	opacity: 0.8;
	padding: 4em 0.7em;
	background-color: white;
	cursor: pointer;
}

#nav-menu > button:focus{
	outline: none;
}
#nav-menu > button:hover{
	background-color: var(--light-blue);
}
#popup-background{
	box-sizing: border-box;
	position: fixed;
	background-color: rgba(255, 255, 255, 0.8);
	height: 100%;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}

#popup-container{
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

#popup-container button{
	border-color: var(--light-blue);
	border-style: solid;
	border-radius: 0.3em;
	border-width: 0.1em;
	background-color: white;
}

#popup-container button:hover{
	background-color: var(--light-blue);
}

#popup-container button:focus{
	outline: none;
}

#popup-container button.back{
	margin: 2em 0;

}

.popup{
	display: flex;
	flex-direction: column;
	align-items: flex-end;
}

.popup > button{
	margin: 0.2em;
}

.item-form{
	display: flex;
	flex-direction: column;
}

.item-form > input{
	margin: 0.1em;
	padding: 0.2em;
	border-color: var(--light-blue);
	border-width: 0.01em;
	border-style: solid;
	box-shadow: none;
}

.item-form > input:focus{
	outline: none;
}
#display {
	box-sizing: content-box;
	padding: 1em;
	width: 25em;
	max-height: 45em;
}

#display h2 {
	color: var(--dark-blue);
}

.item-display {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
}

.todo-list,
.item-display .contact-list {
	padding: 2em;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	border: solid 0.1em;
	border-radius: 0.5em;
	border-color: var(--dark-blue);
	margin: 1em;
	height: 25em;
}

#display button {
	border: none;
	padding: 0.3em 0.7em;
	border-radius: 0.2em;
	align-self: flex-end;
	margin: 0 1.5em 0 0;
	cursor: pointer;
}

.todo-list-item {
	display: flex;
	align-items: center;
	cursor: pointer;
}

.checkbox {
	width: 1em;
	height: 1em;
	color: var(--green);
	cursor: pointer;
	border: solid 1px var(--light-blue);
	border-radius: 0.2em;
	margin: 0.2em;
}

.checkbox.done:before{
	content: "\2713";
	position: relative;
	top: -0.2em;
	left: 0.05em;
}

#display button.delete{
	margin: 0 0 0 0.2em !important;
	align-self: center !important;
	padding: 0 !important;
	width: 1.2em;
	height: 1.2em;
	background-color: white;
	color: #600;
	cursor: pointer;
}

.contact-list .contact{
	cursor: pointer;
}

.tag-list{
	display: flex;
	flex-wrap: wrap;
}

.tag-list .item{
	cursor: pointer;
	margin: 0.2em 0.5em;
}

.collection > div{
	cursor: pointer;
}
:root{
	--yellow: #F2A104;
	--green: #00743F;
	--light-blue: #72A2C0;
	--blue: #1D65A6;
	--dark-blue: #192E5B;
}

* {
	font-family: 'Alegreya';
	box-sizing: border-box;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

#app{
	display: inline-flex;
	transform: scale(1, 1);
}

button:focus{
	outline: none;
}

button{
	cursor: pointer;
}
